<template>
   <div class="container d-flex align-items-center justify-content-center min-vh-100">
        <div class="column w-50">
            <div style="overflow-y: scroll; height: 300px;" class="border">
                <dl class="row" v-for="message in messages" :key="message.id">
                    <dt class="col-sm-3">{{ message.userName }}:</dt>
                    <dd class="col-sm-9">{{ message.message }}</dd>
                </dl>
            </div>
        
            <!-- <ul>
                <li v-for="user in users">{{ user }}</li>
            </ul> -->

            <!-- <form> -->
                <div v-if="showInput" class="form-group text-center">
                    <input ref="userName" v-model="userName" type="text" class="form-control" placeholder="Имя пользователя">
                    <button @click="connectToChat()" class="form-control btn btn-primary">Подключиться</button>
                </div>

                <input ref="userNameHidden" hidden="true">

                <div v-if="!showInput" class="form-group text-center">
                    <input ref="messageInput" v-model="messageInput" type="text" class="form-control" placeholder="Сообщение">
                    <button @click="sendMessage()" class="form-control btn btn-primary">Отправить</button>
                </div>
            <!-- </form> -->
        </div>
    </div>
</template>

<script>
export default {
  data(){

        return{
            showInput : true,
            users : [],
            messages : [],
        }
    },

  methods : {
        connectToChat(){
            this.users.push(this.$refs.userName.value)
            this.$refs.userNameHidden.value = this.$refs.userName.value

            this.showInput = false

            console.log(this.$refs.userName.value)

            return true
        },
    
        sendMessage(){
            console.log("message: " + this.$refs.messageInput.value)
            console.log("userName: " + this.$refs.userNameHidden.value)

            var message = {
                id: this.messages.length,
                userName:   this.$refs.userNameHidden.value,
                message: this.$refs.messageInput.value
            }

            this.messages.push(message)
        }
    }
}
</script>

